
.weather {
margin: 0 auto;
}
.svg {
  float:right;
  width:60px;
  margin: 0 auto;
  padding: 10px 10px 0 20px;
}
.temp {
  font-size: 15px;
  float: right;
  margin: 10px, 5px;
  padding-top: 0px;
  padding-right: 10px;
}
:root { --sw: 10px;/*等同于设置所有的path line 等stroke-width="10"*/ --a: 30deg;
}
svg {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  fill: none;
  stroke: white;
  stroke-width: var(--sw);
  stroke-linecap: round;
}
mask :nth-child(odd) {
  fill: white;
}
mask :nth-child(2n) {
  fill: black;
  stroke: black;
  stroke-width: calc(2*var(--sw));
}
[id='sun'] {
  --i: -2;
  -webkit-mask: url(#m);
          mask: url(#m);
}
.osc,
line {
  -webkit-animation: osc 2s ease-in-out infinite alternate;
          animation: osc 2s ease-in-out infinite alternate;
}
line {
  -webkit-animation: exp 2s ease-in-out infinite alternate;
          animation: exp 2s ease-in-out infinite alternate;
}
[id='raindrop'] {
  -webkit-animation: fall-clockwise 2s ease-in-out infinite;
          animation: fall-clockwise 2s ease-in-out infinite;
}
[id='rays'] {
  -webkit-animation: rot 2s linear infinite;
          animation: rot 2s linear infinite;
}


/*上下晃动特效*/
@-webkit-keyframes osc {
to {
    -webkit-transform: translatey(calc(var(--i, 1)*var(--sw)));
            transform: translatey(calc(var(--i, 1)*var(--sw)));
}
}
@keyframes osc {
to {
    -webkit-transform: translatey(calc(var(--i, 1)*var(--sw)));
            transform: translatey(calc(var(--i, 1)*var(--sw)));
}
}


/*旋转特效*/
@-webkit-keyframes rot {
to {
    -webkit-transform: rotate(var(--a));
            transform: rotate(var(--a));
}
}
@keyframes rot {
to {
    -webkit-transform: rotate(var(--a));
            transform: rotate(var(--a));
}
}


/*变大*/
@-webkit-keyframes exp {
to {
    -webkit-transform: scalex(1.5);
            transform: scalex(1.5);
}
}
@keyframes exp {
to {
    -webkit-transform: scalex(1.5);
            transform: scalex(1.5);
}
}


/*下落特效*/
@-webkit-keyframes fall-clockwise {
0% {
    -webkit-transform: translateY(-7px) scale(0.8);
    transform: translateY(-7px) scale(0.8);
    opacity: 0;
}
10% {
    -webkit-transform: translateY(0px) translateX(4px) scale(0.7);
    transform: translateY(0px) translateX(4px) scale(0.7);
    opacity: 1;
}
45% {
    -webkit-transform: translateY(7px) translateX(0px) scale(0.6);
    transform: translateY(7px) translateX(0px) scale(0.6);
}
50% {
    opacity: 1;
}
100% {
    opacity: 0;
    -webkit-transform: translateY(14px) translateX(3px) scale(0.5);
    transform: translateY(14px) translateX(3px) scale(0.5);
}
}
@keyframes fall-clockwise {
0% {
    -webkit-transform: translateY(-7px) scale(0.8);
    transform: translateY(-7px) scale(0.8);
    opacity: 0;
}
10% {
    -webkit-transform: translateY(0px) translateX(4px) scale(0.7);
    transform: translateY(0px) translateX(4px) scale(0.7);
    opacity: 1;
}
45% {
    -webkit-transform: translateY(7px) translateX(0px) scale(0.6);
    transform: translateY(7px) translateX(0px) scale(0.6);
}
50% {
    opacity: 1;
}
100% {
    opacity: 0;
    -webkit-transform: translateY(14px) translateX(3px) scale(0.5);
    transform: translateY(14px) translateX(3px) scale(0.5);
}
}



