
.header {
  position: relative;
  overflow: hidden;
  color: #fff;
  width: 100%;
  height: 100px;
}
.time {
  position: relative;
  float: left;
  width: 55%;
  margin-top: 20px;
}
.time span {
  display: block;
  text-align: left;
}
.day {
  float: left;
  font-size: 4em;
  margin-right: 10px;
}
.week {
  margin-top: 10px;
  font-size: 1.3em;
  font-weight: 700;
  padding-bottom: 5px;
}
.content-wrapper {
  float: right;
  width: 130px;
}



